.Home {
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: space-between;
}


.starStatusTxtWrap {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.starStatusTxt {
    margin-bottom: 16px;
    color: #2f64ff;
    background-color: white;
    font-weight: 700;
    border: 1px solid #e9e9e9;
    padding: 2px 30px;
    border-radius: 5px;
}

.star_wrap {
    background-color: #2f64ff;
    display: flex;
    justify-content: center;
    flex: 1;
    align-items: center;
    flex-direction: column;
}

.star_wrap .star {
    display: block;
    transform: translate(40px, 0);
    height: 220px;
    width: 260px;
    background-repeat: no-repeat;
}

.star_wrap .star_on_2 {
    background-image: url("../image/vpn_on_icon_Normal.png");
}

.star_wrap .star_on_0 {
    background-image: url("../image/vpn_on_icon0_Normal.png");
}

.star_wrap .star_on_1 {
    background-image: url("../image/vpn_on_icon1_Normal.png");
}

.star_wrap .star_off {
    background-image: url("../image/vpn_off_icon_Normal.png");
}
.setting{
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    left: 5px;
    top:8px;
    width: 44px;
    height: 44px;
}