body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  overflow: hidden;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  background: white;
  height: 100vh;
}

#root {
  position: relative;
}
@media only screen and (min-width: 768px) {
  /* For desktop: */
  #root {
    width:450px;
    margin:0 auto
  }
}


@media only screen and (min-width: 600px) {
  /* For tablets: */

}

::-webkit-scrollbar { display: none; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.Home {
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: space-between;
}


.starStatusTxtWrap {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.starStatusTxt {
    margin-bottom: 16px;
    color: #2f64ff;
    background-color: white;
    font-weight: 700;
    border: 1px solid #e9e9e9;
    padding: 2px 30px;
    border-radius: 5px;
}

.star_wrap {
    background-color: #2f64ff;
    display: flex;
    justify-content: center;
    flex: 1 1;
    align-items: center;
    flex-direction: column;
}

.star_wrap .star {
    display: block;
    transform: translate(40px, 0);
    height: 220px;
    width: 260px;
    background-repeat: no-repeat;
}

.star_wrap .star_on_2 {
    background-image: url(/static/media/vpn_on_icon_Normal.8a49d364.png);
}

.star_wrap .star_on_0 {
    background-image: url(/static/media/vpn_on_icon0_Normal.cfe2932b.png);
}

.star_wrap .star_on_1 {
    background-image: url(/static/media/vpn_on_icon1_Normal.5447e335.png);
}

.star_wrap .star_off {
    background-image: url(/static/media/vpn_off_icon_Normal.7174f382.png);
}
.setting{
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    left: 5px;
    top:8px;
    width: 44px;
    height: 44px;
}
.Login .weui-label{
    width: 60px;
}
.Logs li{
    text-align: left;
    margin-bottom: 8px;
    font-size: 12px;
    border-bottom: 1px solid #e1e1e1;
}
.Logs ul{
    padding:8px
}
.Setting .weui-cell__bd{
    text-align: left;
}

